.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  margin: 0 auto;
  min-width: 1000px;
  font-family: "SegoeUI", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #FFFFFF;
  background-color: #131316;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "Segoe UI";
  src: url("../fonts/SegoeUI.woff2") format("woff2"), url("../fonts/SegoeUI.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Segoe UI-Bold";
  src: url("../fonts/SegoeUI-Bold.woff2") format("woff2"), url("../fonts/SegoeUI-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.main-header {
  position: relative;
}

.main-header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 542px;
  z-index: -1;
  background: linear-gradient(180deg, rgba(19, 19, 22, 0) 0%, #131316 100%);
}

.main-header__wrapper {
  margin: 0 auto;
  padding: 20px 190px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__button {
  padding: 10px 16px;
}

.page__body-image {
  position: absolute;
  z-index: -1;
  width: 100%;
}

.product {
  background-color: #FFFFFF;
}

.product__info {
  text-align: center;
}

.product__title {
  color: #131316;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  margin: 0 auto;
  padding-bottom: 5px;
}

.product__description {
  color: #131316;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  margin: 0 auto;
  padding-bottom: 5px;
}

.product__link {
  display: block;
  margin: 0 auto;
  padding: 10px;
  letter-spacing: 0.06em;
  margin: 0 10px 10px;
}

.products {
  padding: 22px 0 10px;
  display: grid;
  grid-template-columns: repeat(3, 200px);
  gap: 10px;
  list-style: none;
  justify-content: center;
}

.products__item {
  border-radius: 4px;
}

.button {
  color: #FFFFFF;
  text-transform: uppercase;
  background: linear-gradient(rgba(128, 26, 179, 0) -91.42%, #4C4CFF 99.79%);
  cursor: pointer;
  border-radius: 4px;
  text-align: center;
}
.button:hover {
  background: #4C4CFF;
}

.main-container {
  margin: 0;
  padding: 0 190px;
}

.main-container__title {
  margin: 0 auto;
  text-align: center;
  font-size: 32px;
  line-height: 42px;
  font-weight: 400;
}

.description__new-cell {
  font-size: 24px;
  line-height: 32px;
  padding-left: 39px;
  font-weight: 700;
}

.description__cell {
  text-decoration-line: line-through;
  color: #D9D9D9;
}

.description__price {
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
}

.products-next {
  display: grid;
  grid-template-columns: repeat(2, 305px);
  gap: 10px;
  justify-content: center;
}

.products-next__list {
  border-radius: 4px;
}

.product-next {
  background-color: #FFFFFF;
}

.discount__title {
  margin: 0 auto;
  text-align: center;
  font-size: 32px;
  line-height: 42px;
  font-weight: 400;
  padding: 53px 0 21px;
  color: #FFFFFF;
}

.discount__wrapper {
  padding: 32px 0 0 10px;
}

.discount {
  color: #131316;
}

.discount__info {
  display: flex;
  justify-content: space-between;
}

.discount__item {
  border-radius: 4px;
  position: relative;
  padding-bottom: 37px;
  width: 620px;
  margin: 0 auto;
}

.discount__heading {
  font-size: 16px;
  line-height: 21px;
  font-style: normal;
  font-weight: 400;
  margin: 0;
  padding: 9px 0;
}

.discount__description {
  margin: 0;
  font-size: 13px;
  line-height: 16px;
  padding-bottom: 18px;
}

.discount__link-product {
  display: block;
  margin: 0 auto;
  padding: 19px 10px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #131316;
  border: 1px dashed #131316;
  width: 259px;
  text-align: center;
  cursor: pointer;
  border-radius: 4px;
  margin-bottom: 10px;
}
.discount__link-product:hover {
  color: #FFFFFF;
  background: #4C4CFF;
}

.discount__link {
  margin: 0 auto;
  width: 259px;
  display: block;
  padding: 19px 10px;
}

.specification__row {
  display: flex;
  justify-content: space-between;
  width: 237px;
}

.specification__old-price {
  text-decoration-line: line-through;
  color: #D9D9D9;
}

.specification__new-price {
  font-weight: 700;
}

.specification__percent {
  margin-left: 13px;
  background-color: #FF0000;
  border-radius: 4px;
  color: #FFFFFF;
  padding: 1px 10px;
  margin-bottom: 20px;
}

.promo__row {
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  width: 240px;
  padding-bottom: 1px;
}

.promo__price-save {
  color: #FF0000;
  font-weight: 700;
}

.social__title {
  margin: 0 auto;
  text-align: center;
  padding: 61px 0 16px;
  font-size: 15px;
  font-weight: 400;
  color: #FFFFFF;
}

.social__list {
  display: flex;
  margin: 0 auto;
  padding: 0;
  justify-content: center;
}

.social__link {
  margin-right: 8px;
  margin-left: 7px;
}

.social__icon {
  background-color: #FFFFFF;
  border-radius: 40px;
  border: 2px #131313;
}
.social__icon:hover {
  background-color: #bbbaba;
}

.main-footer {
  margin: 0;
  padding: 0 190px;
  margin-bottom: 20px;
}

.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contacts__list {
  padding: 0;
  margin: 0;
}

.contacts__phone {
  display: flex;
}

.contacts__phone-text {
  color: #FFFFFF;
}

.contacts__mail {
  color: #FFFFFF;
}

.contacts__title {
  font-size: 11px;
  margin: 0;
  margin-top: 20px;
}

.footer-navigation {
  margin-top: 19px;
}